<template>
  <div class="p-3">
    <p class="text-center">Selecione a empresa</p>
    <div class="list-group shadow-sm">
      <button type="button" class="list-group-item list-group-item-action active">
        Empresas
      </button>
      <button
        type="button"
        class="list-group-item list-group-item-action text-center px-2"
        v-for="empresa in empresas" :key="empresa.id"
        @click="selecionarEmpresa(empresa)"
      >
        <p class="mb-0 text-muted">{{empresa.cnpj.substr(0,2) + '.' + empresa.cnpj.substr(2,3) + '.' + empresa.cnpj.substr(5,3) + '/' + empresa.cnpj.substr(8,4) + '-' + empresa.cnpj.substr(12,2)}}</p>
        <p class="mb-0" style="font-weight: 600">{{empresa.nome}}</p>
        <p class="mb-0 small">{{empresa.matriz === 1 ? 'Sede matriz' : 'Filial'}}</p>
      </button>
    </div>
    <div class="text-center mt-3">
      <router-link :to="{name: 'recadpj-menu'}" class="btn btn-primary">Voltar</router-link>
    </div>
  </div>
</template>

<script>
  export default {
    name: "Empresas",
    data: () => ({
      empresas: [],
    }),
    created() {
      this.empresas = this.$store.state.pj;
    },
    methods: {
      selecionarEmpresa(empresa) {
        this.$router.push({ name: 'recadpj-1', params: {empresa: empresa} });
      }
    },
  }
</script>

